// 
// topbar.scss
//

// Logo
.logo {
    display: block;
    line-height: $topbar-height;

    span.logo-lg {
        display: block;
    }

    span.logo-sm {
        display: none;
    }

    .logo-lg-text-dark {
        color: $dark;
        font-weight: $font-weight-bold;
        font-size: 22px;
        text-transform: uppercase;
    }

    .logo-lg-text-light {
        color: $white;
        font-weight: $font-weight-bold;
        font-size: 22px;
        text-transform: uppercase;
    }
}

.logo-box {
    height: $topbar-height;
    width: $leftbar-width;
    padding: 0 25px;
    background-color: $bg-leftbar-light;
    position: fixed;
    z-index: 1;
    text-align: center;
}

.navbar-custom {
    background-color: $body-bg;
    padding: 0 15px;
    height: $topbar-height;
    z-index: 100;

    .topnav-menu {
        >li {
            float: left;
        }

        .nav-link {
            padding: 0 15px;
            color: $gray-700;
            min-width: 32px;
            display: block;
            line-height: $topbar-height;
            text-align: center;
            max-height: $topbar-height;
        }
    }

    /* Search */
    .app-search {
        overflow: hidden;
        height: $topbar-height;
        display: table;
        max-width: 180px;
        margin-right: 20px;

        .app-search-box {
            display: table-cell;
            vertical-align: middle;

            input::-webkit-input-placeholder {
                font-size: 0.8125rem;
                color: $gray-500;
            }
        }

        .form-control {
            border: none;
            height: 38px;
            padding-left: 20px;
            padding-right: 0;
            color: $dark;
            background-color: $light;
            box-shadow: none;
            border-radius: 30px 0 0 30px;
        }

        .input-group-append {
            margin-left: 0;
            z-index: 4;
        }

        .btn {
            background-color: $light;
            border-color: transparent;
            color: $text-muted;
            border-radius: 0 30px 30px 0;
            box-shadow: none !important;
        }
    }

    .button-menu-mobile {
        border: none;
        color: $dark;
        display: inline-block;
        height: $topbar-height;
        line-height: $topbar-height;
        width: 60px;
        background-color: transparent;
        font-size: 24px;
        cursor: pointer;
    }

    .button-menu-mobile.disable-btn {
        display: none;
    }
}


/* Notification */
.noti-scroll {
    max-height: 230px;
}

.notification-list {
    margin-left: 0;

    .noti-title {
        padding: 15px 20px;
    }

    .noti-icon {
        font-size: 21px;
        vertical-align: middle;
    }

    .noti-icon-badge {
        display: inline-block;
        position: absolute;
        top: 16px;
        right: 10px;
    }

    .notify-item {
        padding: 12px 20px;

        .notify-icon {
            float: left;
            height: 36px;
            width: 36px;
            font-size: 18px;
            line-height: 36px;
            text-align: center;
            margin-right: 10px;
            border-radius: 50%;
            color: $white;
        }

        .notify-details {
            margin-bottom: 5px;
            overflow: hidden;
            margin-left: 45px;
            text-overflow: ellipsis;
            white-space: nowrap;
            color: $gray-800;

            b {
                font-weight: 500;
            }

            small {
                display: block;
            }

            span {
                display: block;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: 13px;
            }
        }

        .user-msg {
            margin-left: 45px;
            white-space: normal;
            line-height: 16px;
        }
    }

    .profile-dropdown {
        .notify-item {
            padding: 7px 20px;
        }
    }
}

.profile-dropdown {
    width: 170px;

    i {
        vertical-align: middle;
        margin-right: 5px;
    }
}

.nav-user {
    padding: 0 12px !important;

    img {
        height: 32px;
        width: 32px;
    }
}

// Topbar Dark
.navbar-custom-dark {
    background-color: $primary !important;

    .logo-box {
        background-color: $primary;
    }

    .dropdown.show {
        .nav-link {
            background-color: rgba($white, 0.03);
        }
    }

    .button-menu-mobile {
        color: $white;
    }

    /* Search */
    .app-search {
        input::-webkit-input-placeholder {
            color: rgba($white, 0.7) !important;
        }

        .form-control {
            color: $white;
            background-color: rgba($white, 0.15);
        }

        .btn {
            background-color: rgba($white, 0.15);
            color: rgba($white, 0.7);
        }
    }
}

//topbar dark Content Page
.topbar-dark {
    .content-page {
        margin-top: 100px;
    }
}


.page-title-main {
    margin-bottom: 6px;
}

@media (max-width: 620px) {
    .page-title-main {
        display: none !important;
    }
}



@media (max-width: 768px) {
    .button-menu-mobile.disable-btn {
        display: block !important;
    }
}

@media (max-width: 1024px) {
    .content-page {
        padding: 0;

        .content {
            padding: 80px 10px 0 10px;
        }
    }

    .navbar-custom {
        background-color: $white;
        position: fixed;
        left: 0;
        right: 0;
        box-shadow: $box-shadow;
        z-index: 999;

        .button-menu-mobile.disable-btn {
            display: inline-block;
        }

        .page-title-main {
            font-size: 16px;
            margin-bottom: 6px;
            margin-top: 24px;
            display: block;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
        }

        .breadcrumb {
            display: none;
        }
    }
}

// Dark Topnav
.dark-topnav {
    .navbar-custom {
        background-color: $bg-topbar-dark;
    }
}